<template>
  <div>
    <i-cell-group>
      <i-cell v-for="item in routeList" :key="item" :title="item" is-link :url="href+`${item}`">
      </i-cell>
    </i-cell-group>
  </div>
</template>
<script>
import routeList from '../../../route-list.js'
export default {
  data(){
    return {
      routeList,
    }
  },
  computed: {
    href() {
      return location.href
    }
  },
}
</script>




